import { ButtonGroup, Button } from "@material-ui/core";
import { useState } from "react";
import { ControlKeys, ControlCommands, ButtonStyle } from "../../util/constants";
import Alert from "../Alert";

interface PowerProps{
    handleCommand: (e: ControlKeys) => void
}
export default function Power(props: PowerProps){
    const {handleCommand} = props
    const [openPowerOnAlert, setOpenPowerOnAlert]= useState(false)
    const [openPowerOffAlert, setOpenPowerOffAlert]= useState(false)
    return(
        <>
        <ButtonGroup key='shutters' color="primary" variant="outlined">
        <Button  key='power off'  style={ButtonStyle}  onClick={(e) => setOpenPowerOffAlert(true)} >Power Off</Button>
        <Button  key='power on' style={ButtonStyle}  onClick={(e) => setOpenPowerOnAlert(true)} >Power On</Button>
    </ButtonGroup>
    <Alert open={openPowerOffAlert} handleCommand={handleCommand} handleClose={()=>setOpenPowerOffAlert(false)}
        header='Power Off' message='Are you sure?' command={ControlCommands.POWER_OFF}/>
        <Alert open={openPowerOnAlert} handleCommand={handleCommand} handleClose={()=>setOpenPowerOnAlert(false)}
        header='Power On' message='Are you sure?' command={ControlCommands.POWER_ON}/>
        </>
    )
}