import { ButtonGroup, Button } from "@material-ui/core"
import { ControlKeys, ControlCommands, ButtonStyle } from "../../util/constants"

interface ShutterProps{
    handleCommand: (e: ControlKeys) => void
}
export default function Shutter(props: ShutterProps){
    const {handleCommand} = props
    return (
        <ButtonGroup key='power' color="primary" variant="outlined">
        <Button  key='shutter_on' style={ButtonStyle} onClick={(e) => handleCommand(ControlCommands.SHUTTER_CLOSED)} >Sutter Closed (i)</Button>
        <Button  key='sutter_off' style={ButtonStyle}  onClick={(e) => handleCommand(ControlCommands.SHUTTER_OPEN)} >Shutter Open (o)</Button>
        </ButtonGroup>
    )
}