    import { Dialog, DialogTitle, Button, ButtonGroup} from "@material-ui/core";
import { CSSProperties } from "react";
import { ControlKeys, ControlCommands } from '../util/constants'
import OSDPostion from "./CommandInterfaces/OSDPostion";
import Testpattern from "./CommandInterfaces/Testpattern";
import BackColor from './CommandInterfaces/BackColor'
interface MenuDialog_Props {
    open: boolean,
    handleClose: () => void,
    handleCommand: (e: ControlKeys) => void
}

export default function MenuDialog(props: MenuDialog_Props) {
    const { open, handleClose, handleCommand } = props
   
    const buttonStyle: CSSProperties = {
        padding: '20px',
        width: '100px',
       
    }
    const buttonGroupStyle: CSSProperties = {
        justifyContent: 'center',
        textAlign: 'center'
        
    }
    return (
        <Dialog key='menu_dialog' onClose={handleClose} aria-labelledby="simple-dialog-title" open={open}>
            <DialogTitle style={buttonGroupStyle} key='menu_dialog_title' id="simple-dialog-title">Menu Commands</DialogTitle>
            <ButtonGroup style={buttonGroupStyle} key='menu_main_keys' variant="outlined">
                <Button style={buttonStyle} key='menu' onClick={(e) => handleCommand(ControlCommands.MENU)} >Menu</Button>
                <Button style={buttonStyle} key='enter' onClick={(e) => handleCommand(ControlCommands.MENU_DEFAULT_KEY)} >Default</Button>
            </ButtonGroup>

            <br></br>

            <ButtonGroup style={buttonGroupStyle} key='menu_row1_keys' variant="outlined">
                <Button style={buttonStyle} key='up' onClick={(e) => handleCommand(ControlCommands.MENU_UP_KEY)} >Up</Button>
            </ButtonGroup>

            <ButtonGroup style={buttonGroupStyle} key='menu_row2_keys' variant="outlined">
                <Button style={buttonStyle} key='left' onClick={(e) => handleCommand(ControlCommands.MENU_LEFT_KEY)} >Left</Button>
                <Button style={buttonStyle} key='enter' onClick={(e) => handleCommand(ControlCommands.MENU_ENTER_KEY)} >Enter</Button>
                <Button style={buttonStyle} key='right' onClick={(e) => handleCommand(ControlCommands.MENU_RIGHT_KEY)} >Right</Button>
            </ButtonGroup>

            <ButtonGroup style={buttonGroupStyle} key='menu_row3_keys' variant="outlined">
                <Button style={buttonStyle} key='down' onClick={(e) => handleCommand(ControlCommands.MENU_DOWN_KEY)} >Down</Button>
            </ButtonGroup>

            <ButtonGroup style={buttonGroupStyle} key='menu_row4_keys' variant="outlined">
                <Button style={buttonStyle} key='osd on' onClick={(e)=>handleCommand(ControlCommands.OSD_ON)}>OSD ON</Button>
                <Button style={buttonStyle} key='osd off' onClick={(e)=>handleCommand(ControlCommands.OSD_OFF)}>OSD OFF</Button>
                <Button style={buttonStyle} key='freeze on' onClick={(e)=>handleCommand(ControlCommands.FREEZE_ON)}>Freeze ON</Button>
                <Button style={buttonStyle} key='freeze off' onClick={(e)=>handleCommand(ControlCommands.FREEZE_OFF)}>Freeze Off</Button>
            </ButtonGroup>


           <Testpattern handleCommand={handleCommand} />
           <OSDPostion handleCommand={handleCommand}/>
           <BackColor handleCommand={handleCommand}/>

        </Dialog>
    );
}

