import { TableCell, TableRow } from "@material-ui/core";
import { PJ_OBJ } from "../util/constants";
import BorderColorIcon from '@material-ui/icons/BorderColor';

interface tableProps{
    pj_tupple:  {pj:PJ_OBJ,selected: boolean, active: boolean}
}

export default function ProgrammerTable(props: tableProps){
    const {pj_tupple} = props
    let pj = pj_tupple.pj

    const blendRegion =()=>{
        let color = ''
        let regions = [pj.edgeBlendingUpper, pj.edgeBlendingRight, pj.edgeBlendingLower, pj.edgeBlendingLeft]
        for(let i=0; i < regions.length; i++){
            switch(regions[i]){
                case 'On':
                    color+= ' green'
                    break;
                case 'Off':
                    color+= ' black'
                    break
                case 'Unknown':
                    color+= ' yellow'
                    break
                default:
                    color+=' red'
            }
        }
      
        return (
            <p key={pj.id+'bp'} style={{ padding: 10,  margin:'auto', textAlign: 'center', borderStyle: 'solid', borderColor: color}}> {pj.edgeBlending}
             <BorderColorIcon key={pj.id+'blend'} style={{fontSize:'15', marginLeft: '5', marginTop: 'auto', color:pj.edgeBlendingMarker==='On'?'red':'gray'}} /></p>
        )
    }
    return (
        <TableRow key={pj.id+'row'}>
            <TableCell key={pj.id+'name'} component='th' scope='row'>
                {pj.name}
            </TableCell>
            <TableCell key={pj.id+'id'}>{pj.id}</TableCell>
            <TableCell key={pj.id+'shutter'}>{pj.shutter}</TableCell>
            <TableCell key={pj.id+'blendregion'}>{blendRegion()}</TableCell>
            <TableCell key={pj.id+'markers'}>{pj.edgeBlendingMarker}</TableCell>
            <TableCell key={pj.id+'test'}>{pj.testPattren}</TableCell>
            <TableCell key={pj.id+'lamp'}>{pj.lampStatus}</TableCell>
            <TableCell key={pj.id+'sig'}>{pj.hdmiResolution}/{pj.hdmiSignalLevel}/{pj.hdmiVerticalFrequency}</TableCell>
            <TableCell key={pj.id+'osd'}>{pj.osdPostion}</TableCell>

        </TableRow>
    )
}