import {Button, Dialog, DialogActions, DialogTitle, DialogContent, DialogContentText, TextField } from "@material-ui/core";
import { useEffect, useState } from "react";
interface Alert_Props{
    open: boolean,
    handleInput: (text: string)=>void,
    handleClose: ()=>void,
    header: string,
    message: string,
    startText: string | undefined
}
export default function Alert(props: Alert_Props){
    const {open,handleInput, handleClose, header, message ,startText} = props
    const [text, setText] = useState('')

    useEffect(()=>{
      if(startText)
      setText(startText)
    },[startText])

    const handleClick=(approval: boolean)=>{
        if(approval){
            handleInput(text)
        }
        handleClose()
        setText('')

    }
    return (
        <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">{header}</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            <span style={{width:'50'}}>{message}</span>
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <TextField value={text} onChange={(e)=>setText(e.target.value)}></TextField>
          <Button onClick={(e)=>handleClick(false)} color="primary">
            Cancle
          </Button>
          <Button onClick={(e)=>handleClick(true)} color="primary" autoFocus>
            Okay
          </Button>
        </DialogActions>
      </Dialog>
        
    )
}