import * as C from './constants'
//const puppeteer = require('puppeteer');

async function goFetch(query: string){
    let fetchString = C.pjServer+'api/'+query
   // console.log(fetchString)
    try{
    let res = await fetch(fetchString,{
       mode: 'no-cors'
    })
    
   // console.table(res.body)
    if(res.ok){
      //  console.log(await res.text())
        //return await res.json()
        return await res.json()
    }
}catch(e){
    console.log(e)
}
    
    
}

export async function getPJ(target:number) {
    let pj = await goFetch('status/?pj='+target)
    if(pj){
        return pj as unknown as C.PJ_OBJ
    }
    return undefined
    
}

export async function getPJs(){
    let pjs = await goFetch('status/?pj=all')
    if(pjs){
        return pjs as unknown as Record<number,C.PJ_OBJ>
    }
}
export async function getRigStatus(){
   // console.log('Getting Rig Status')
    let status = await goFetch('status/?rigstatus=all')
    if(status){
        return status as unknown as C.RIG_STATUS
    }
}
