export async function setConfig(query: string){
    await goFetch('config/?'+query)
}
export async function setter(target: number, command:  C.ControlKeys) {
    console.log('Setting ',target,command)
    await goFetch('set/?pj='+target+'&command='+command)
}
export async function  powerOff(target: number) {
     return await setter(target, C.ControlCommands.POWER_OFF)
}

export async function powerOn(target: number) {
    return await setter(target,C.ControlCommands.POWER_ON)
}

export async function shutterOn(target:number){
    console.log('Sutter On')
    return await setter(target,C.ControlCommands.SHUTTER_OPEN)
}
export async function shutterOff(target:number) {
    return await setter(target,C.ControlCommands.SHUTTER_CLOSED)
}

