import {Button, ButtonGroup, Paper} from '@material-ui/core'
import './numberpad.css'

import * as C from '../util/constants'
interface numberpad_props{
    handlePad: (e: string)=>void
}
export default function Numberpad(props:numberpad_props){
    const {handlePad} =props
    
    const getButtons=()=>{
     
        return C.keys.map(key=>{
            let w = key === C.ioCommands.STORE_GROUP? '125px' : '75px'
            return <Button variant="contained" style={{margin: 2, padding: '10px', width: w, touchAction: 'manuplation'}} size={'large'} key={key} onClick={(e)=>handlePad(key)} >{key}</Button>     
        })
    }

    const buttonGroups=()=>{
        let buttons = getButtons()
        let groups =[]
        let chunk = 3
        for(let i=0; i<=buttons.length-chunk; i+=chunk){
           
            let g = buttons.slice(i,chunk+i)
           
            groups.push(
                <ButtonGroup key={'pad_group'+i}>
                   {g}
                </ButtonGroup>
             
            )
        }
        return groups
    }
    return (
        <Paper style={{touchAction: 'manuplation'}}elevation={1} className='pad'>
            
            {buttonGroups()}
            {getButtons()[C.keys.length-1]}
            
        </Paper>

    )
}