
import { Card, Dialog, Paper, Typography, makeStyles, CardContent, Theme, createStyles, AppBar } from '@material-ui/core';

import updateLog from '../util/UpdateLog'
interface About_Props{
    open: boolean
    handleClose: ()=>void
}
const useStyles = makeStyles((theme: Theme)=> createStyles({
    root: {
      minWidth: 275,
      flexGrow: 1,
    },
    bullet: {
      display: 'inline-block',
      margin: '0 2px',
      transform: 'scale(0.8)',
    },
    menuButton: {
        marginRight: theme.spacing(2),
    },
    title: {
      fontSize: 14,
    },
    pos: {
      marginBottom: 12,
    },
  }));
export default function About(props: About_Props){
    const {open,handleClose}=props;
    const classes = useStyles();
    const bull = <span className={classes.bullet}></span>;
    const getNotes=(notes: string[])=>{
        return notes.map(note=>{
           return <Typography key={note} variant='body2'>
                {bull}{note}
            </Typography>
        })
    }
    const getUpdates=()=>{
        return updateLog.map(update=>{
            return <Card className={classes.root} key={update.version}>
                <CardContent>
                    <Typography variant='h6' component='h2'>
                        {update.version}
                    </Typography>
                    <Typography className={classes.pos}>
                        {update.date}
                    </Typography>
                    {getNotes(update.notes)}
               
                </CardContent>
                
                </Card>
        })
    }
    return(
        <Dialog open={open} onClose={handleClose}>
            <div className={classes.root}>
                <AppBar position='static'>
                <Typography variant='h4'>vanPJ</Typography>
              <Typography className={classes.pos} >Logic.Lighting | MIT Licence</Typography>
                    </AppBar>
          <Paper style={{width: '50%'}}>  
              
        {getUpdates()}
        </Paper>
        </div>
        </Dialog>
    )
}