import { Button} from '@material-ui/core'
import { useMemo } from 'react'
import * as C from '../util/constants'

interface worlds_props{
    setWorld: (g: string)=> void
    name: string
    groupEditCMD: C.ioCommands | undefined
    groups: C.Groups
    selected?: number
}
/*
interface worldTuppel{
  key: string,
  name: string,
  selected: boolean
}*/
export default function Worlds( props: worlds_props){

    const {setWorld, groups, groupEditCMD, selected} = props

    const getWorlds = useMemo(()=>{
        let g= Object.entries(groups).map(group=>{
          return {key: group[0], name: group[1].name, selected: selected ===parseInt(group[0])  }
       
        })
      if(groupEditCMD === C.ioCommands.STORE_GROUP){
        g.push({key:C.ioCommands.STORE_GROUP, name: 'Add Group', selected: false})
      }
      return g
    },[groups,groupEditCMD,selected])

    const getButtons =()=>{
   
      return getWorlds.map(group=>{
        return <Button key={group.key} onClick={(e)=>{e.preventDefault(); setWorld(group.key)}} variant={group.selected?'outlined':'text'}>{group.name}</Button>
      })
    }
    return (
        <div {...props} >
          {getButtons()}
          </div>
    )
}
