import { Button, Paper, ButtonGroup, } from "@material-ui/core";
import {useState} from 'react'
import { ButtonStyle, HandleCommand, PJs} from "../util/constants";
import BlendDialog from "./BlendDialog";
import MenuDialog from './MenuDialog'
import LensDialog from './LensDialog'
import Power from './CommandInterfaces/Power'
import Shutter from "./CommandInterfaces/Shutter";
import ProjectorSetupDialog from "./ProjectorSetupDialog";
import { useEffect } from "react";

interface command_props{
    handleCommand: HandleCommand
    active: number[]
    pjs: PJs
}
export default function CommandPad(props:command_props){
    const {handleCommand, active, pjs } = props
    const [menuOpen, setMenuOpen] = useState(false)
    const [blendOpen, setBlendOpen] = useState(false)
    const [lensOpen,setLensOpen] = useState(false)
    const [pjSetupOpen,setPJSetupOpen] =useState(false)

    useEffect(()=>{
        if(active.length!==1 || !pjs[active[0]]){
            setPJSetupOpen(false)
        }
    },[active, pjs])
    
    const getPJSetup=()=>{
        if(active.length===1)
            return (<Button key='pj_setup' variant="contained"  style={ButtonStyle} onClick={((e)=>setPJSetupOpen(!pjSetupOpen))}>PJ Setup</Button>)
        
            return (<Button key='pj_setup' variant="contained" disabled style={ButtonStyle} onClick={((e)=>setPJSetupOpen(!pjSetupOpen))}>PJ Setup</Button>)

    }
    const getPJ=()=>{
        return pjs[active[0]]
    }
    return(
       <>
        <Paper  key='commandKeys' style={{width:'300px'}}>
           <Shutter handleCommand={handleCommand}/>
            <br></br>
           <Power handleCommand={handleCommand}/>
            <ButtonGroup color="primary" variant="outlined">
                <Button color="primary" key='menu' variant="contained" style={ButtonStyle}  onClick={(e) => setMenuOpen(!menuOpen)} >Menu</Button>
                <Button color="primary" key='blend' variant="contained" style={ButtonStyle}  onClick={(e) => setBlendOpen(!blendOpen)} >Blend</Button>
            </ButtonGroup>
            <ButtonGroup color='primary' variant='outlined'>
                <Button key='lens' variant="contained" style={ButtonStyle} onClick={((e)=>setLensOpen(!lensOpen))}>Lens</Button>
                {getPJSetup()}
            </ButtonGroup>
            
            <MenuDialog open={menuOpen} handleClose={()=>setMenuOpen(false)} handleCommand={handleCommand}/>
            <BlendDialog open={blendOpen} handleClose={()=>setBlendOpen(false)} handleCommand={handleCommand}/>
            <LensDialog open={lensOpen} handleClose={()=>setLensOpen(false)} handleCommand={handleCommand} />
            <ProjectorSetupDialog open={pjSetupOpen} handleClose={()=>setPJSetupOpen(false)} pj={getPJ()} handleCommand={handleCommand} />
        </Paper>
       
        </>
    )
}
