import * as C from "../util/constants";
import { DataGrid } from '@material-ui/data-grid';
import { Button, Paper, makeStyles, ButtonGroup } from '@material-ui/core'
import AddIcon from '@material-ui/icons/Add';
import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/Delete';
import { useState } from "react";
import { v4 } from 'uuid'
import Alert from './Alert'
import PatchAddDialog from './PatchAddDialog'

interface Patch_props {
  patch: C.Patch
  setPatch?: (p: C.Patch) => void
}

const useStyles = makeStyles((theme) => ({
  fab: {
    position: 'absolute',
    bottom: theme.spacing(2),
    right: theme.spacing(2),
  },

}));
const columns = [
  { field: 'uuid', headerName: 'uuid', width: 100, hide: true },
  { field: 'id', headerName: 'ID', width: 90 },
  { field: 'ip_address', headerName: 'IP Address', width: 170 },
  { field: 'port', headerName: 'Port', width: 130 },
  { field: 'auth', headerName: 'Auth', width: 130 },
  { field: 'make', headerName: 'Make', width: 130 },

];

export default function Patch(props: Patch_props) {
  const { patch, setPatch } = props
  const [addWindowOpen, setAddWindowOpen] = useState(false)
  const [editWindowOpen, setEditWindowOpen] = useState(false)
  const [selected, setSelected] = useState<number[]>([])
  const [deleteWindowOpen, setDeleteWindowOpen] = useState(false)
  const classes = useStyles()
  let newPJs: C.Patch = {}

  const addPJs = (amount: number, startID: number, baseIP: string, port: number, auth: string, make: C.PROJECTOR_MAKE) => {
    setAddWindowOpen(false)
    console.log('Got PJs to make!')
    for (let id = startID; id < amount + startID; id++) {
      let ip_address = baseIP + id
      //console.log('Patch', patch)
      if (Object.values(patch).filter(pj => {
        if (pj.id === id) {
          console.log('Conflicting ID', id);
          return true
        }
        if (pj.ip_address === ip_address) {
          console.log('Conflicting IP', ip_address);
          return true
        }
        return false
      }
      ).length > 0) {
        console.log('Conflicting PJ')
        newPJs[v4()] = { ip_address: '-', port, auth, make, id: 0 }
      } else {
        // console.log('new PJ!')
        newPJs[v4()] = { ip_address, id, port, auth, make }
      }
    }
    //console.log(newPJs)
    let newPatch = Object.assign(patch, newPJs) as C.Patch
    if (newPatch && setPatch) {
      setPatch(newPatch)
    }
  }
  /*
  const getRows = () => {
    return Object.entries(patch).map(e => {
      let pj = e[1]
      //@ts-ignore
      pj.channel = pj.id
      //@ts-ignore
      pj.id = e[0]

      return pj

    })
  }
  */

  const handleDelete = (c: C.ControlKeys) => {

    let newPatch = Object.assign(patch) as C.Patch
    Object.entries(newPatch).forEach(e => {
      if (selected.includes(e[1].id)) {
        delete newPatch[e[0]]
        let newSelect = Object.assign(selected) as number[]
        delete newSelect[e[1].id]
        setSelected(newSelect)
      }
    })


    //const i = Object.assign(newPatch)
    if (newPatch && setPatch) {
      //console.log('Deleting',Object.values(patch).length-Object.values(newPatch).length)
      setPatch(newPatch)
      setSelected([])
    }

  }
  const getSelectedString = () => {
    let s = ''
    for (let i = 0; i < selected.length; i++) {
      s += selected[i] + ' '
      if (i % 9 === 0) {
        s += '\r'
      }
    }
    return s
  }



  return (
    <>
      <Paper style={{ height: 700, width: '100%' }}>
        {patch ?
          <DataGrid rows={Object.values(patch)} columns={columns} checkboxSelection selectionModel={selected} onSelectionModelChange={(e) => setSelected(e.selectionModel as number[])} />
          : <></>
        }

        <ButtonGroup color='primary' className={classes.fab} >
          <Button aria-label="add" onClick={(e) => setAddWindowOpen(true)}> <AddIcon /></Button>
          <Button aria-label="edit" disabled={selected.length < 1} onClick={(e) => setEditWindowOpen(true)}> <EditIcon /></Button>
          <Button aria-label="edit" disabled={selected.length < 1} onClick={(e) => setDeleteWindowOpen(true)}> <DeleteIcon /></Button>
        </ButtonGroup>


      </Paper>
      <Alert open={deleteWindowOpen} handleCommand={handleDelete} handleClose={() => setDeleteWindowOpen(false)} command={C.ControlCommands.MENU} header='Delete Selected' message={'Are you srue you want to delete: \r \n' + getSelectedString()} ></Alert>
      <PatchAddDialog open={addWindowOpen} handleClose={() => setAddWindowOpen(false)} addPJs={addPJs} currentPatch={patch} />
    </>
  )
}