
import Cookies from 'js-cookie'
export interface prefres{
    darkMode: boolean,
    clearProgrammerAfterCMD: boolean,
    showOnlySelected: boolean,
    programmerTableView: boolean
}
const cookieName= 'prefs'
//const prefersDarkMode = useMediaQuery('(prefers-color-scheme: dark)');
export const defaultPrefs: prefres={
    darkMode: true, //prefersDarkMode,
    clearProgrammerAfterCMD: false,
    showOnlySelected: false,
    programmerTableView: false
}

export const setCookie=(prefs: prefres)=>{
    Cookies.set(cookieName,prefs)
}
const getCookie =()=>{
    return Cookies.get(cookieName)
    /*
    let c = Cookies.get(cookieName)
    if(c){
        return JSON.parse(c) 
       // return c as unknown as prefres
    } else {
        setCookie(defaultPrefs)
        return defaultPrefs
    }
    */
}

export function getPrefrences(darkMode=true):prefres{
    let c = getCookie()
    if(c){
        return JSON.parse(c)
    }else{
        let d = defaultPrefs
        d.darkMode = darkMode
        setCookie(d)
        return d
    }
     
    //return getCookie()
}



export function setCookie_DarkMode(mode: boolean){
    let c = getPrefrences()
    c.darkMode=mode
    setCookie(c)

}
export function setCookie_ClearProgrammerAfterCMD(mode: boolean){
    let c = getPrefrences()
    c.clearProgrammerAfterCMD = mode
    setCookie(c)
}
export function setCookie_ShowOnlySelected(mode:boolean){
    let c =getPrefrences()
    console.log(mode)
    c['showOnlySelected'] = mode
    setCookie(c)
}

export function getDarkMode(){
    return getPrefrences().darkMode
}

export function getClearProgrammerAfterCMD(){
    return getPrefrences().clearProgrammerAfterCMD
}
export function getShowOnlySelected(){
    return getPrefrences().showOnlySelected
}

export function getProgrammerTableView(){
    return getPrefrences().programmerTableView
}