import {ControlKeys, ControlCommands} from '../../util/constants'
import {FormControl, InputLabel, Select, MenuItem, makeStyles} from '@material-ui/core'

interface OSDProps{
    handleCommand: (e: ControlKeys) => void
}


const useStyles = makeStyles((theme) => ({
    formControl: {
      margin: theme.spacing(1),
      minWidth: 120,
    },
    selectEmpty: {
      marginTop: theme.spacing(2),
    },
  }));
export default function OSDPostion(props: OSDProps){
    const {handleCommand} = props
    const classes = useStyles()

    return (
        <>
    <FormControl className={classes.formControl}>
    <InputLabel id='osd_position'>OSD Position</InputLabel>
    <Select labelId='osd_position' id='osd_postion'
     value={''} onChange={((e)=> handleCommand(e.target.value as ControlKeys))}>
         <MenuItem value={ControlCommands.OSD_POSITION_UPPER_LEFT}>Upper Left</MenuItem>
         <MenuItem value={ControlCommands.OSD_POSITION_CENTER_LEFT}>Center Left</MenuItem>
         <MenuItem value={ControlCommands.OSD_POSITION_LOWER_LEFT}>Lower Left</MenuItem>
         <MenuItem value={ControlCommands.OSD_POSITION_TOP_CENTER}>Top Center</MenuItem>
         <MenuItem value={ControlCommands.OSD_POSITION_CENTER}>Center</MenuItem>
         <MenuItem value={ControlCommands.OSD_POSITION_LOWER_CENTER}>Lower Center</MenuItem>
         <MenuItem value={ControlCommands.OSD_POSITION_UPPER_RIGHT}>Upper Right</MenuItem>
         <MenuItem value={ControlCommands.OSD_POSITION_LOWER_RIGHT}>Lower Right</MenuItem>
     </Select>
     
</FormControl>
</> )
}