import { Dialog, DialogTitle , TextField, Select, MenuItem, InputLabel, Button} from "@material-ui/core";
import React, { CSSProperties, useState } from "react";
import {PROJECTOR_MAKES, PROJECTOR_MAKE, Patch} from '../util/constants'
interface PatchAdd_Props{
    open: boolean,
    handleClose: ()=>void,
    addPJs: (amount: number, startID: number, baseIP: string, port: number, auth: string, make: PROJECTOR_MAKE)=>void,
    currentPatch: Patch
}

const style: CSSProperties={
    width: '90%',margin: 'auto', marginBottom: '5px'
    
}
export default function PatchAddDialog(props: PatchAdd_Props){
    const {open,handleClose, addPJs, currentPatch}= props

    const getMaxID=()=>{
        let i = -1
        Object.values(currentPatch).forEach(pj=>{
            if(pj.id> i)
                i = pj.id
        })
        if(i===-1)
            return 100
        
            return i
    }

    const [make, setMake]= useState<PROJECTOR_MAKE>(PROJECTOR_MAKES.PANASONIC)
    const [Amount, setAmount] = useState({value:1,err:''})
    const [StartID, setID] = useState({value:getMaxID()+1,err:''})
    const [BaseIP, setBaseIP]= useState({value:'192.168.10.',err:''})
    const [UserName, setUserName] = useState('admin1')
    const [Pass, setPass] = useState('')
    const [Port, setPort] = useState({value:1024, err:''})
    //const [hasErrors, setHasErrors]= useState(false)

   
    const getListItems=()=>{
        return Object.values(PROJECTOR_MAKES).map(key=>{
            //console.log(key)
            return <MenuItem key={key} value={key}>{key}</MenuItem>
        })
    }
    const handleSubmit=()=>{
        let auth = ''
        if(make === PROJECTOR_MAKES.PANASONIC){
            auth = UserName+":"+Pass
        }
        addPJs(Amount.value,StartID.value,BaseIP.value,Port.value, auth,make)
    }
    const handleMake=(e: React.ChangeEvent<{value:unknown}>)=>{
        setMake(e.target.value as PROJECTOR_MAKE)
    }
    const getBaseStart=()=>{
        return parseInt(BaseIP.value.split('.')[3])
    }
    const checkOverlap=(startID: number, amount: number)=>{
        for(let i = startID; i<amount+startID; i++){
            let t = false
            Object.values(currentPatch).forEach(pj=>{
                if(pj.id === i)
                    t = true
            })
            if(t)
                return true
        }
        return false
    }
    const handleAmount=(e: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>)=>{
        let value = parseInt(e.target.value)
        let err= ''
        if(value<1 || isNaN(value)){
            err='Invalid Number'
        } else if(checkOverlap(StartID.value, value)){
            err='Will Overlap Other IDs'
        }

        setAmount({value,err})
    }
    
    const handleID=(e: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>)=>{
        let value = parseInt(e.target.value)
        let err= ''
        if(value<1 || isNaN(value)){
            err='Invalid Number'
        } else if(getBaseStart()+Amount.value >254){
            err='Will Land Outside Base IP Range'
        } else if(checkOverlap(value, Amount.value)){
            err='Will Overlap Other IDs'
        }
        setID({value,err})
    }
    const handlePort=(e: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>)=>{
        let value = parseInt(e.target.value)
        let err= ''
        if(value<1 || isNaN(value)){
            err='Invalid Number'
        }
        setPort({value,err})
    }
    const handleBaseIP=(value: string)=>{
        
        let err= ''
        let s = value.split('.')
        if(s.length===4){
            for(let i =0; i<4;i++){
                if(parseInt(s[i])<0 || parseInt(s[i])>(254-Amount.value)){
                    err ='Invalid Number Range: '+s[i]
                }
            }
        } else{
            err= 'Invalid Number of .'
        }
        console.log(value)
        
        setBaseIP({value,err})
    }

    const handlePass=(value: string)=>{
        setPass(value)
    }
    const handleUser=(value:string)=>{
        setUserName(value)
    }
    return( 
        <Dialog fullWidth={true} style={{padding: '10%'}} onClose={handleClose} aria-labelledby="simple-dialog-title" open={open}>
        <DialogTitle id="simple-dialog-title">Add Projectors</DialogTitle>
        <InputLabel style={style} id="pjMake">Model</InputLabel>
        <Select style={style} id='pjMake' onChange={handleMake} value={make}>
        {getListItems()}
        </Select>
        <br></br>
        <TextField style={style} error={Amount.err? true: false} variant="filled" type={'number'}label={'Amount'} value={Amount.value} onChange={handleAmount} helperText={Amount.err}> </TextField>
        <br></br>
        <TextField style={style} error={StartID.err? true: false} value={StartID.value} helperText={StartID.err} variant="filled" type={'number'} label={'StartID'} onChange={handleID} > </TextField>
        <br></br>
        <TextField style={style} error={BaseIP.err? true: false} value={BaseIP.value} helperText={BaseIP.err} variant="filled" type={'text'}label={'BaseIP'} onChange={(e)=>handleBaseIP(e.target.value)} > </TextField>
        <br></br>
        <TextField style={style} error={Port.err? true: false} value={Port.value} helperText={Port.err} variant="filled" type={'number'}label={'Port'} onChange={handlePort} > </TextField>
        <br></br>
        <TextField style={style}  value={UserName}  variant="filled" label={'Username'} type={'text'} onChange={(e)=>handleUser(e.target.value)}> </TextField>
        <br></br>
        <TextField style={style}  value={Pass}  variant="filled" label={'Password'}type={'text'} onChange={(e)=>handlePass(e.target.value)}> </TextField>

        <Button style={{width:'20%', margin: 'auto', marginBottom:'2%'}} disabled={Amount.err || BaseIP.err || StartID.err? true: false} variant="contained" color="primary" onClick={handleSubmit}>Submit</Button>
      </Dialog>
    )
}