
import { Socket } from 'socket.io-client'
import * as C from './constants'
//import {socket} from '../LoadPage'
type RIG_STATUS = React.Dispatch<React.SetStateAction<C.RIG_STATUS>>
type PJS = React.Dispatch<React.SetStateAction<C.PJs>>
type CONFIG = React.Dispatch<React.SetStateAction<C.Config>>
type POLLING_PROGRESS = React.Dispatch<React.SetStateAction<number>>

export default function SocketListener(
    socket: Socket,
    setStatus: RIG_STATUS,
    setPJs: PJS,
    setConfig: CONFIG,
    setPollingProgress: POLLING_PROGRESS,
) {
   
    socket.on('connected', () => {
        console.log('Connected!')
        // socket.emit('Hello')
    })
    socket.on('Hello', () => {
        //console.log('Hello!')
    })
    socket.on(C.ioCommands.EMITTING_PJS, (pjs: C.PJs) => {
        setPJs(pjs)
         console.log('Got new PJs!')
    })
    socket.on(C.ioCommands.EMITTING_STATUS, (status: C.RIG_STATUS) => {
       // console.log('Status Updated')
        setStatus(status)
    })
    socket.on(C.ioCommands.REQUEST_UPDATE, () => {
        console.log('Requesting Update!')
        socket?.emit(C.ioCommands.REQUESTING_UPDATE)
    })

    socket.on(C.ioCommands.REQUEST_CONFIG, () => {
        console.log('Requesting Config')
       // socket?.emit(C.ioCommands.REQUESTING_CONFIG)
    })
    socket.on(C.ioCommands.EMITTING_CONFIG, (config: C.Config) => {
        console.groupCollapsed('Got New Config')
        console.log('Config', config)
        console.log('Patch', config.Patch)
        console.groupEnd()
        setConfig(config)
        /*
        if(config.Groups[0] && config.Groups[0].group)
            setWorld(config.Groups[0].group)
            */
    })
    socket.on(C.ioCommands.EMITTING_POLLING_PROGRESS, (progress: number) => {
        setPollingProgress(progress)
    })
}
    



export function SocketPatcher(socket: Socket, patch: C.Patch) {
    socket.emit(C.ioCommands.EMITTING_PATCH, patch)
}   