
import * as C from './constants'



const log=(s:string)=>{
    console.log(s)
}
let _world : number[] =[]
let _groups :C.Groups = {}
export function processCMD(cmdLine: string, selected: number[], world: number[], groups: C.Groups) {
    _world = world
    _groups= groups
    cmdLine = cmdLine.trim()
    let cmds = cmdLine.split(' ')
    console.group('CMDs '+cmds)
  
    let processed = parse(cmds)
    log('Parsed: '+processed)
    processed = handleSelected(processed,selected)
    log('Filtred: '+processed)

    processed = processed.filter(p=> world.includes(p))
    log('Worlds: '+processed)
    console.groupEnd()
    return processed
}

function handleSelected(processed: number[], selected: number[]):number[]{
    let newSelected = selected
    
    for(let i=0; i<processed.length; i++){
        let p = processed[i]
        if(p<0 && newSelected.includes(p*-1)){
            newSelected = newSelected.filter((n)=> n!== (p*-1))
        } else if(!newSelected.includes(p) && p>0){
            newSelected.push(p)
        }
    }
    return newSelected
    
}

function parse(cmdArray: string[], parsed: number[] = []): number[] {
    
    if (cmdArray.length === 0){ 
        //log('Parsing Tree Complete')
        console.groupEnd()
        return parsed;
    }
    console.group('Parsing: '+cmdArray+ ' |  '+parsed)
    switch (cmdArray[0]) {
        case C.commands.THRU:
            log('Parsing THRU')
            return parseThru(cmdArray, parsed)
        case C.commands.GROUP:
            log('Parsing GROUP')
            return parseGroup(cmdArray,parsed)
        case '-':
            log('Parsing Negative')
            return parseNegative(cmdArray, parsed)
        case '+':
            log('Parsing Postive')
            return parsePostive(cmdArray,parsed)
        default:
            log('Parsing Number')
            return parseNumber(cmdArray, parsed)
    }
}
function parseNumber(cmdArray: string[], parsed: number[]): number[] {
    if (!cmdArray) return []
    let i = Number.parseInt(cmdArray[0])
    
    if (isFinite(i)) {
        parsed.push(i)
     //   log('parsedNumber: '+i+' | '+parsed)
        return parse(cmdArray.slice(1), parsed)
    }
    console.error('Invalid Number', i)
    return parsed

}

function parsePostive(cmdArray:string[], parsed: number[]):number[]{
    if(!cmdArray) return []
    let i = 1
  //  log('CMD Lenght: '+cmdArray.length)
  //  log(i+' '+cmdArray[i])
    while(cmdArray[i+1] !=='+' && cmdArray[i+1] !== '-' && i< cmdArray.length){
        i++
      //  log(i+' '+cmdArray[i]+' '+(cmdArray[i]==='+'))
    }
    let slice = []
    if(i===1){
        slice = cmdArray.slice(1,2)
    } else{
        slice = cmdArray.slice(1,i+1)
    }
    log(cmdArray+' | Postive Slice ('+ cmdArray[i] + ' @ '+i+') '+slice)
    let sub = parse(slice)

    
 //   log('Sub '+sub+ ' | '+cmdArray[i+1]+' i:'+ i + ' ' +cmdArray.slice(i+1))
    return parse(cmdArray.slice(i+1),parsed.concat(sub))
}

function parseNegative(cmdArray: string[], parsed: number[]): number[] {
  
        if(!cmdArray) return []
        let i = 1
   //     log('CMD Lenght: '+cmdArray.length)
   //    log(i+' '+cmdArray[i])
        while(cmdArray[i+1] !=='+' && cmdArray[i+1] !== '-' && i< cmdArray.length){
            i++
     //       log(i+' '+cmdArray[i]+' '+(cmdArray[i]==='+'))
        }
        let slice = []
        if(i===1){
            slice = cmdArray.slice(1,2)
        } else{
            slice = cmdArray.slice(1,i+1)
        }
        log(cmdArray+' | Negative Slice ('+ cmdArray[i] + ' @ '+i+') '+slice)
        let sub = parse(slice)
 
        for(let n=0; n<sub.length;n++){
            sub[n] = sub[n]*-1
        }
     //   log('Sub '+sub+ ' | '+cmdArray[i+1]+' i:'+ i + ' ' +cmdArray.slice(i+1))
        return parse(cmdArray.slice(i+1),parsed.concat(sub))

}
function parseThru(cmdArray: string[], parsed:number[]):number[] {
    if(cmdArray.length ===0) return []
    let i = parsed.pop()
    if(!i) { 
        console.error('Invalid THRU Placement. Number Must Procced THRU CMD')
        return []
    }
  //  log(''+cmdArray.slice(1))
    let n = parse(cmdArray.slice(1,2))[0]
    if(!n){
        n = Math.max(..._world)
        //console.error('Invaild THRU Placement. Number Must Follow THRU CMD', n)
        //return []
    }
    let sub = []
    let negative = 1
    if(i<0){
        i = i*-1
        negative = -1
    }
    if(i<n){
        for(let x = i; x<=n;x++){
            sub.push(x*negative)
        }
    } else{
        for(let x=n; x<=i; x++){
            sub.push(x*negative)
        }
    }
    return parse(cmdArray.slice(2),parsed.concat(sub))

}
function parseGroup(cmdArray: string[], parsed:number[]):number[]{
    if(cmdArray.length < 1) return []
    log(cmdArray+' '+parseInt(cmdArray[0])+'')
    let g = _groups[parseInt(cmdArray[1])]
    console.table(_groups)
    if(!g) return []
    log('Group '+g)
    return parse(cmdArray.slice(3),parsed.concat(g.group))
}