import { ButtonGroup, Button } from "@material-ui/core";
import { useState } from "react";
import { ControlKeys, ControlCommands, ButtonStyle } from "../../util/constants";
import Alert from "../Alert";

interface Props{
    handleCommand: (e: ControlKeys) => void
}
export default function LensCal(props: Props){
    const {handleCommand} = props
    const [openHomeAlert, setOpenHomeAlert]= useState(false)
    const [openCalAlert, setOpenCalAlert]= useState(false)
    return(
        <div style={{margin:'auto'}}>
        <ButtonGroup key='shutters' color="primary" variant="outlined">
        <Button  key='home'  style={ButtonStyle}  onClick={(e) => setOpenHomeAlert(true)} >Lens Home</Button>
        <Button  key='cal' style={ButtonStyle}  onClick={(e) => setOpenCalAlert(true)} >Lens Cal</Button>
    </ButtonGroup>
    <Alert open={openHomeAlert} handleCommand={handleCommand} handleClose={()=>setOpenHomeAlert(false)}
        header='Home Lense' message='Are you sure?' command={ControlCommands.LENS_POSTION_HOME}/>
        <Alert open={openCalAlert} handleCommand={handleCommand} handleClose={()=>setOpenCalAlert(false)}
        header='Cal Lens' message='Are you sure?' command={ControlCommands.LENS_POSTION_HOME}/>
        </div>
    )
}