import { Dialog, DialogTitle, Button, ButtonGroup } from "@material-ui/core";
import { CSSProperties } from "react";
import { ControlKeys, ControlCommands } from '../util/constants'
interface BlendDialog_Props {
    open: boolean,
    handleClose: () => void,
    handleCommand: (e: ControlKeys) => void
}

export default function BlendDialog(props: BlendDialog_Props) {
    const { open, handleClose, handleCommand } = props
    const buttonStyle: CSSProperties = {
        padding: '20px',
        width: '140px'

       
    }
    const buttonGroupStyle: CSSProperties = {
        justifyContent: 'center',
        textAlign: 'center',
        margin: 'auto'
       
        
    }
    return (
        <Dialog key='blend_dialog'  onClose={handleClose} aria-labelledby="simple-dialog-title" open={open}>
            <DialogTitle style={buttonGroupStyle} key='menu_dialog_title' id="simple-dialog-title">Blending</DialogTitle>
            <div key='blend_div' style={{width:300, justifyContent: 'center', textAlign: 'center'}}>
            <ButtonGroup style={buttonGroupStyle} key='blend_main_keys' variant="outlined">
                <Button style={buttonStyle} key='blend_on' onClick={(e) => handleCommand(ControlCommands.EDGE_BLENDING_ON)} >Edge Blend On</Button>
                <Button style={buttonStyle} key='blend_off' onClick={(e) => handleCommand(ControlCommands.EDGE_BLENDING_OFF)} >Edge Blend Off</Button>
            </ButtonGroup>

            <br></br>

            <ButtonGroup style={buttonGroupStyle} key='makers_main_keys' variant="outlined">
                <Button style={buttonStyle} key='markers_on' onClick={(e) => handleCommand(ControlCommands.EDGE_BLENDING_MARKERS_ON)} >Markers On</Button>
                <Button style={buttonStyle} key='markers_off' onClick={(e) => handleCommand(ControlCommands.EDGE_BLENDING_MARKERS_OFF)} >Markers Off</Button>
            </ButtonGroup>

            <br></br>

            <ButtonGroup style={buttonGroupStyle} key='upper_keys' variant="outlined">
                <Button style={buttonStyle} key='upper_on' onClick={(e) => handleCommand(ControlCommands.EDGE_BLENDING_UPPER_ON)} >Uppere On</Button>
                <Button style={buttonStyle} key='upper_off' onClick={(e) => handleCommand(ControlCommands.EDGE_BLENDING_UPPER_OFF)} >Uppper Off</Button>
            </ButtonGroup>

            <ButtonGroup style={buttonGroupStyle} key='right_keys' variant="outlined">
                <Button style={buttonStyle} key='right_on' onClick={(e) => handleCommand(ControlCommands.EDGE_BLENDING_RIGHT_ON)} >Right On</Button>
                <Button style={buttonStyle} key='right_off' onClick={(e) => handleCommand(ControlCommands.EDGE_BLENDING_RIGHT_OFF)} >Right Off</Button>
            </ButtonGroup>

            <ButtonGroup style={buttonGroupStyle} key='lower_keys' variant="outlined">
                <Button style={buttonStyle} key='lower_on' onClick={(e) => handleCommand(ControlCommands.EDGE_BLENDING_LOWER_ON)} >Lower On</Button>
                <Button style={buttonStyle} key='lower_off' onClick={(e) => handleCommand(ControlCommands.EDGE_BLENDING_LOWER_OFF)} >Lower Off</Button>
            </ButtonGroup>

            <ButtonGroup style={buttonGroupStyle} key='left_keys' variant="outlined">
                <Button style={buttonStyle} key='left_on' onClick={(e) => handleCommand(ControlCommands.EDGE_BLENDING_LEFT_ON)} >Left On</Button>
                <Button style={buttonStyle} key='left_off' onClick={(e) => handleCommand(ControlCommands.EDGE_BLENDING_LEFT_OFF)} >Left Off</Button>
            </ButtonGroup>
            </div>
        </Dialog>
    );
}