import {Button, Dialog, DialogActions, DialogTitle, DialogContent, DialogContentText } from "@material-ui/core";
import { ControlKeys } from "../util/constants";
interface Alert_Props{
    open: boolean,
    handleCommand: (cmd: ControlKeys)=>void,
    handleClose: ()=>void,
    command: ControlKeys,
    header: string,
    message: string
}
export default function Alert(props: Alert_Props){
    const {open,handleCommand, command, handleClose, header, message} = props

    const handleClick=(approval: boolean)=>{
        if(approval){
            handleCommand(command)
        }
        handleClose()

    }
    return (
        <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">{header}</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            <span style={{width:'50'}}>{message}</span>
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={(e)=>handleClick(false)} color="primary">
            Cancle
          </Button>
          <Button onClick={(e)=>handleClick(true)} color="primary" autoFocus>
            Okay
          </Button>
        </DialogActions>
      </Dialog>
        
    )
}