import { Button, } from "@material-ui/core";
import { CSSProperties } from "react";

import { PJ_OBJ } from "../util/constants";



interface PJCardProps{
    pj_tupple: {pj:PJ_OBJ,selected: boolean, active: boolean}
    handlePJCard: (pjID: number) => void;
}
export default function PJCard(props:PJCardProps){
    const {pj_tupple, handlePJCard} = props
    let pj = pj_tupple.pj

    //const classes = useStyles();

    const cardStyle: CSSProperties={
      
        background: pj_tupple.active? 'gray': 'white',
        border: pj_tupple.selected? 'thick solid cornflowerBlue' : 0,
        color: pj_tupple.active || pj_tupple.selected? 'lightwhite': 'black',   
        width: 80,
        height: 50
    }
    
    return(
       
       <Button variant='contained'  style={cardStyle} onClick={()=>handlePJCard(pj.id)}>{pj.id}
       </Button>
       
       
    )
}
