import { Button, Menu, MenuItem, FormControlLabel, Switch  } from '@material-ui/core'
//import {SettingsIcon} from '@material-ui/icons/Settings';
import SettingsIcon from "@material-ui/icons/Settings";
import { useState, useRef} from 'react'
import { prefres } from '../prefrences';
import * as C from '../util/constants'


interface menu_props {
  handleSettings: (e: prefres) => void,
  prefs: prefres,
  handleOpenConfig: ()=>void
}
export default function ControlMenu(props: menu_props) {

 // const value = useContext(Settings)
  //console.log(value)
  const { prefs, handleSettings, handleOpenConfig } = props
  const divRef = useRef(null)

  const [open, setOpen] = useState(false)
  

  const handleOpen = () => {
    setOpen(true)


  }
  const handleClose = (e: C.menuCommands | undefined) => {
    setOpen(false)
  }

  const handleChange = (e: C.menuCommands) => {
    
    let p = Object.assign({},prefs)
    switch (e) {
      case C.menuCommands.CLEAR_SELECTED_ON_CMD:
        p.clearProgrammerAfterCMD = !prefs.clearProgrammerAfterCMD
        break;
      case C.menuCommands.IF_SELECTED:
        p.showOnlySelected = !prefs.showOnlySelected
        break;
      case C.menuCommands.DARK_MODE:
        p.darkMode = !prefs.darkMode
        break;
      case C.menuCommands.PROGRAMMER_TABLE_VIEW:
        p.programmerTableView = !prefs.programmerTableView
        break;
    }
    handleSettings(p)
  }
  let button =
    <Button ref={divRef} aria-controls="simple-menu" aria-haspopup="true" onClick={() => handleOpen()}>
      <SettingsIcon />
    </Button>
  if(process.env.NODE_ENV === 'development'){
    document.title = 'PJ Dev'
  }
  return (
    <div>

      {button}


      <Menu
        id="simple-menu"
        keepMounted
        //@ts-ignore
        anchorEl={divRef.current}
        open={open}
        onClose={() => handleClose(undefined)}
      >
        <MenuItem >
          <FormControlLabel
            control={<Switch key={C.menuCommands.CLEAR_SELECTED_ON_CMD} checked={prefs.clearProgrammerAfterCMD}
              onClick={(e) => handleChange(C.menuCommands.CLEAR_SELECTED_ON_CMD)} name="clearSelectedOnPress" />}
            label="Clear Programmer On CMD"
          />
        </MenuItem>
        <MenuItem>

          <FormControlLabel
            control={<Switch key={C.menuCommands.IF_SELECTED} checked={prefs.showOnlySelected}
              onChange={(e) => handleChange(C.menuCommands.IF_SELECTED)} name="showonlyselected" />}
            label="Show Only Selected"
          />
        </MenuItem>
        <MenuItem>

          <FormControlLabel
            control={<Switch key={C.menuCommands.DARK_MODE} checked={prefs.darkMode}
              onChange={(e) => handleChange(C.menuCommands.DARK_MODE)} name="darkMode" />}
            label="Dark Mode"
          />
       
        </MenuItem>
        <MenuItem>

          <FormControlLabel
            control={<Switch key={C.menuCommands.PROGRAMMER_TABLE_VIEW} checked={prefs.programmerTableView}
              onChange={(e) => handleChange(C.menuCommands.PROGRAMMER_TABLE_VIEW)} name="Programmer Table View" />}
            label="Programmer Table View"
          />
       
        </MenuItem>
        <MenuItem>
        <Button onClick={(e)=>{setOpen(false); handleOpenConfig()}}>Edit Config</Button>
        </MenuItem>
      </Menu>
     
      <span style={{textAlign:'center', fontSize: 10}}> Projector Controler v{process.env.REACT_APP_VERSION}{process.env.NODE_ENV==='development'?' Beta':''}</span>
    </div>
  )

}