import { Button, ButtonGroup, createStyles, Fade, makeStyles, Menu, MenuItem, Paper, Theme, Typography } from "@material-ui/core";
import Worlds from "./Worlds";
import { Config, ioCommands } from '../util/constants'
import { useRef, useState } from "react";
import SettingsIcon from "@material-ui/icons/Settings";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore"
import PublicIcon from '@material-ui/icons/Public';

import ExpandLessIcon from '@material-ui/icons/ExpandLess'

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {

        },
        container: {
            display: 'flex',
        },
        paper: {
            margin: theme.spacing(1),
        },
    }),
);

interface WorldBox_Props {
    handleWorldSelection: (g: string) => void,
    handleGroupSelection: (g: string) => void,
    handleGroupConfig: (cmd: ioCommands) => void,
    groupEditCMD: ioCommands | undefined,
    config: Config,
    world: number
}
export default function WorldBox(props: WorldBox_Props) {
    const { handleWorldSelection, handleGroupSelection, handleGroupConfig, groupEditCMD, config, world } = props;
    const [worldOpen, setWorldOpen] = useState(false)
    const [groupOpen, setGroupOpen] = useState(false)
    const [configOpen, setConfigOpen] = useState(false)
    const divRef = useRef(null)
    const classes = useStyles();

    const handleConfigButton = () => {
        //@ts-ignore
        divRef.current.focus()

        setConfigOpen(true)

    }
    const handleConfigSelect = (cmd: ioCommands) => {
        setConfigOpen(false)
        handleGroupConfig(cmd)
    }
    const handleWorld = (w: boolean) => {
        setWorldOpen(!w)
        if (!w) {
            setGroupOpen(false)
        }
    }
    const handleGroup = (g: boolean) => {
        setGroupOpen(!g)
        if (!g) {
            setWorldOpen(false)
        }
    }
    const getStyle = () => {
        if (!worldOpen && !groupOpen) {
            return {
                width: '90%', margin: 'auto', height: '40px'
            }
        }
        return {
            width: '90%', margin: 'auto'
        }

    }
    const getHelperText = () => {
        let generalMessage = ' | Hit Clear to Cancle'
        switch (groupEditCMD) {
            case ioCommands.STORE_GROUP:
                return <Typography>Select Group To Store {generalMessage}</Typography>
            case ioCommands.DELETE_GROUP:
                return <Typography>Select Group To Delete {generalMessage}</Typography>
            case ioCommands.LABEL_GROUP:
                return <Typography>Select Group to Label {generalMessage}</Typography>
        }
    }
    return (
        <div className='WorldBox' >
            <Paper style={getStyle()}>
                <ButtonGroup style={{ marginTop: '1px', marginBottom: '2px' }} >
                    <Button key='world_button'
                        onClick={() => handleWorld(worldOpen)}> Worlds {world !==0? <PublicIcon fontSize="small" /> : '  '}
                        {worldOpen ? <ExpandLessIcon /> : <ExpandMoreIcon />} </Button>
                    <Button key='group_button' onClick={() => handleGroup(groupOpen)}>Groups
                        {groupOpen ? <ExpandLessIcon /> : <ExpandMoreIcon />}
                    </Button>
                </ButtonGroup>
                {getHelperText()}

                <div className={classes.container} style={{width:'100%'}}>


                    {worldOpen ? <Fade key='world_Grow' in={worldOpen}>


                        <Worlds key="world_box" setWorld={handleWorldSelection} groupEditCMD={groupEditCMD} groups={config.Groups} name={'Worlds'} selected={world} />
                    </Fade> :
                        <Fade key='group_box' in={groupOpen}>
                            <Worlds setWorld={handleGroupSelection} groupEditCMD={groupEditCMD} groups={config.Groups} name={'Groups'} />
                        </Fade>
                    }
                    {groupOpen || worldOpen ?
                        <Button ref={divRef} style={{ float: 'right', marginLeft:'auto' }} onClick={() => handleConfigButton()}><SettingsIcon /></Button>
                        : <></>}
                </div>

            </Paper>
            <Menu id='groups-menu' keepMounted anchorEl={divRef.current}
                open={configOpen}
                onClose={() => setConfigOpen(false)}>
                <MenuItem>
                    <Button key='add_group' onClick={() => handleConfigSelect(ioCommands.STORE_GROUP)}>Store Group</Button>
                </MenuItem>
                <MenuItem>
                    <Button key='rename_group' onClick={() => handleConfigSelect(ioCommands.LABEL_GROUP)}>Rename Group</Button>
                </MenuItem>
                <MenuItem>
                    <Button key='delete_group' onClick={() => handleConfigSelect(ioCommands.DELETE_GROUP)}>Delete Group</Button>
                </MenuItem>
            </Menu>

        </div>
    )
}
