import { Dialog, DialogTitle, Button, TextField, Container } from "@material-ui/core";
import { CSSProperties, useState, useEffect } from "react";
import { ControlKeys, ControlCommands, HandleCommand, PJ_OBJ } from '../util/constants'
interface Props {
    open: boolean,
    handleClose: () => void,
    handleCommand: HandleCommand
    pj: PJ_OBJ
}

export default function ProjectorSetup(props: Props) {
    const { open, handleClose, handleCommand, pj } = props
    const [name, setName] = useState( '' )
    const [ID, setID] = useState('')
    const buttonStyle: CSSProperties = {
        padding: '20px',
        width: '100px',
        margin: 'auto'
       
    }
    const buttonGroupStyle: CSSProperties = {
        justifyContent: 'center',
        textAlign: 'center'
        
    }

    useEffect(()=>{
        if(pj && pj.name){
            setName(pj.name)
        }
    },[pj])


    const handleClick=(cmd: ControlKeys)=>{
        switch (cmd){
            case ControlCommands.PROJECTOR_NAME:
                handleCommand(cmd, name)
                break
            case ControlCommands.PROJECTOR_ID:
                handleCommand(cmd, ID)
                break;
        }
       
        handleClose()
    }
    
    const getButton=(cmd: ControlKeys, text: string, popUp: boolean)=>{

        return <Button style={buttonStyle} key={text} onClick={(e)=>handleClick(cmd)}>{text}</Button>
    }
    return (
        <Dialog key='setup_dialog' onClose={handleClose} aria-labelledby="simple-dialog-title" open={open}>
            <DialogTitle style={buttonGroupStyle} key='menu_dialog_title' id="simple-dialog-title">PJ Setup</DialogTitle>
           
        <Container key='name'>    
            <TextField key='pj_name_input' label='PJ Name' value={name} onChange={(e)=>setName(e.target.value)}></TextField>
            {getButton(ControlCommands.PROJECTOR_NAME, 'Store Name', true)}
        </Container>    
        <Container key='id'>
            <TextField key='pj_id_input' label='PJ Input' value={ID} onChange={(e)=>setID(e.target.value)}></TextField>
            {getButton(ControlCommands.PROJECTOR_ID, 'Store ID', true)}
        </Container>

        </Dialog>
    );
}