import React, { useEffect, useState } from 'react';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import { createMuiTheme, createStyles, makeStyles, ThemeProvider, Theme } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import App from './App';
import * as Pref from './prefrences';
import ControlMenu from './components/ControlMenu';
import socketIOClinet   from 'socket.io-client'
import { Backdrop } from '@material-ui/core';
import Disconnected from './components/Disconnected';
export const Settings = React.createContext(Pref.defaultPrefs)

export let socket =socketIOClinet()

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    backdrop: {
      zIndex: theme.zIndex.drawer + 1,
      color: '#fff',
    },
  }),
);
export default function LoadPage() {
    const prefersDarkMode = useMediaQuery('(prefers-color-scheme: dark)');
    // const prefersDarkMode = getDarkMode()  
    const classes = useStyles()
    const [prefres, setPrefs] = useState(Pref.defaultPrefs)
    const [openConfig,setOpenConfig] = useState(false)
    const [socketConnected,setSocketConnected]= useState(false)

    useEffect(()=>{
    if(!socketConnected){
      try{
   
          console.log('Connecting Socket...')
       // let s = socketIOClinet()
    
        socket.on('connect',()=>{
           // socket = s
            setSocketConnected(true)
        })
        socket.on('disconnect',()=>{
           // socket = undefined
            setSocketConnected(false)
        })
      
      }catch(e){

      }
    }
    return function cleanup(){
     
    }

    },[socketConnected])

    useEffect(() => {
  
        setPrefs(Pref.getPrefrences(prefersDarkMode))
      
    }, [prefersDarkMode])

    const theme = createMuiTheme({
        palette: {
            type: prefres.darkMode ? 'dark' : 'light',
        }
    })
   
    const handlePrefs = (p: Pref.prefres) => {
       // console.log(prefres)
        setPrefs(p)
      //  console.log(prefres)
        Pref.setCookie(p)
    }
    return (
        <ThemeProvider theme={theme}>
            <Settings.Provider value={prefres}/>
            <CssBaseline />
            <Backdrop className={classes.backdrop} open={!socketConnected} >
                <Disconnected/>
            </Backdrop>
                <ControlMenu handleOpenConfig={()=> setOpenConfig(true)}handleSettings={handlePrefs} prefs={prefres} />
                <App socket={socket} prefs={prefres} openConfig={openConfig} handleCloseConfig={()=>setOpenConfig(false)}/>
              
                
            
        </ThemeProvider>
    );
}